









































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

@Component
export class Card extends Vue {
    //////////////////// Prop ////////////////////

    @Prop({
        type: String, // Boolean, Number, String, Array, Object
        default: () => undefined,
    })
    private headerLabel: string;

    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => false,
    })
    private showHeader: boolean;

    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => false,
    })
    private showCollapse: boolean;

    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => true,
    })
    private visiable: boolean;

    private showCard: boolean = true;

    //////////////////// Vue Life ////////////////////

    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}

    @Watch('headerLabel', { immediate: true, deep: true })
    private headerLabelChanged(newVal: string, oldVal: string): void {}

    @Watch('visiable', { immediate: true, deep: true })
    private visiableChanged(newVal: boolean, oldVal: boolean): void {
        this.showCard = newVal;
    }

    private clickArrow(): void {
        this.showCard = !this.showCard;
    }
}
export default Card;
Vue.component('card', Card);
